.array-container {
    /* position: absolute; */
    /* left: 100px; */
    text-align: center;
    justify-content: center;
    height: 60px;
}
.array-bar {
    width: 7px;
    background-color: blue;
    display: inline-block;
    margin: 0px 1px;

}

.btn {
    border-color: white;    
    border-radius: 20px;
    margin: auto;
    padding: 8px 8px 8px 8px;
    background-color: gold;
}
.slidecontainer {
    width: 100%;
  }
  
  .slider {
    -webkit-appearance: none;
    width: 50%;
    height: 25px;
    background: #d8e8ff;
    outline: none;
    opacity: 0.7;
    -webkit-transition: .2s;
    transition: opacity .2s;
  }
  
  .slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px;
    height: 25px;
    background: blue;
    cursor: pointer;
  }
  
  .slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: #04AA6D;
    cursor: pointer;
  }
  #reset {
    width: 5px;
    background-color: blue;
    display: inline-block;
    margin: 0px 1px;
  }